.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0%,#e1ffea22 60%);
    margin-top: 40px;
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color: #171717;
    font-size: 60px;
    font-weight: 600;

}
.offers-left p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;

}
.offers-left button{
    width: 282px;
    height: 78px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 38px;
}
.offers-right img{
    height: 330px;
}
@media(max-width:1024px){
    .offers{
       padding: 0px 60px;
       margin-bottom: 80px;
       height: 40vh;
    }
    .offers-left h1{
       font-size: 30px;
    }
    .offers-left p{
       font-size: 16px;
    }
    .offers-right img{
        width: 200px;
        height: 230px;
    }
    .offers-left button{
        width: 160px;
        height: 40px;
        font-size: 16px;
    }
    .offers-right{
       padding-top: 10px;
    }
}
@media(max-width:800px){
    .offers{
       margin-bottom: 60px;
    }
    .offers-left h1{
       font-size: 20px;
    }
    .offers-left p{
       font-size: 13px;
    }
    .offers-right img{
        width: 180px;
        height: 230px;
    }
    .offers-left button{
        width: 140px;
        height: 30px;
        font-size: 12px;
    }
   
}
@media(max-width:500px){
    .offers{
        height: 40vh;
        margin-bottom: -5px;
        
    }
    .offers-left h1{
       font-size: 16px;
       width: 250px;
    }
    .offers-left{
        margin-left: -50px;
    }
    .offers-left p{
       font-size: -2px;
    }
    .offers-right img{
        display: none;
    }
    .offers-left button{
        width: 100px;
        height: 30px;
        font-size: 10px;
        margin-top: 12px;
    }
    .offers-right{
       padding-top: 10px;
    }

}
@media(max-width:1280px){
    .offers{
        padding: 0px 80px;
        margin-bottom: 120px;
    }
    .offers-left h1{
        font-size: 35px;
    }
    .offers-left p{
        font-size: 15px;
    }
    .offers-right img{
        width: 250px;
    }
    .offers-left button{
        width: 200px;
        height: 35px;
        font-size: 16px;
        margin-top: 12px;
    }
    .offers-right{
        padding-top: 20px;
    }
}